import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { TABLA_TIPO_RESERVA, PAGINACION_CANTIDAD_DEVOLVER } from './../constantes';
import { MiscelaneoDetalle } from './../miscelaneo/miscelaneoDetalle';
import { MiscelaneoService } from './../miscelaneo/miscelaneo.service';
import { ClienteSelectorComponent } from './../cliente/cliente-selector.component';
import { ReservaService } from './reserva.service';
import { Reserva } from './reserva';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { SelectItem, LazyLoadEvent } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';
import { Cliente } from '../cliente/cliente';

@Component({
    templateUrl: './reserva-listado.component.html'
})
export class ReservaListadoComponent extends BaseComponent implements OnInit {

    @ViewChild(ClienteSelectorComponent) clienteSelectorComponent: ClienteSelectorComponent;
    filtro: Reserva = new Reserva();
    lstTipo: SelectItem[] = [];
    paginacion: ParametroPaginacion = new ParametroPaginacion();

    constructor(
        private router: Router,
        private reservaService: ReservaService,
        private miscelaneoService: MiscelaneoService) {
        super();
    }

    ini() {

    }

    ngOnInit() {
        var filtroTipoReserva = new MiscelaneoDetalle();
        filtroTipoReserva.codigoTabla = TABLA_TIPO_RESERVA;

        this.lstTipo.push({ label: ' -- Todos -- ', value: null });
        this.miscelaneoService.listado(filtroTipoReserva).then(
            res => {
                res.forEach(reg => this.lstTipo.push({ label: reg.descripcion, value: reg.codigoElemento }));
                this.filtro.tipoReserva = null;
            }
        );
    }
    buscar(dt: any) {
        dt.reset();
    }

    nuevo() {
        this.router.navigate(["mavitours/reserva-mantenimiento"]);
    }
    editar(bean: Reserva) {
        this.router.navigate(["mavitours/reserva-mantenimiento", bean.id], { skipLocationChange: true });
    }

    cargarCliente(dtoCliente: Cliente) {
        this.filtro.cliente = dtoCliente.id;
        this.filtro.clienteNombre = dtoCliente.razonSocial;
        this.clienteSelectorComponent.salir();
    }

    iniciarSelectorCliente() {
        this.clienteSelectorComponent.iniciarComponente();
    }

    limpiarCliente() {
        this.filtro.cliente = null;
        this.filtro.clienteNombre = null;
    }

    cargarReservas(event: LazyLoadEvent) {
        console.log("lazyyyy");
        this.bloquearPagina();
        this.paginacion.listaResultado = [];
        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.reservaService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.desbloquearPagina();
            });
    }

    verTraslados(bean: Reserva) {
        this.router.navigate(['mavitours/traslado-listado', bean.id], { skipLocationChange: true });
    }

}



