import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { routes } from './../../shared/shell/app-routes/app.routes';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { ProveedorSaludMantenimientoComponent } from './proveedorsalud-mantenimiento.component';
import { Component, OnInit, ViewChild } from '@angular/core';
import { SelectItem, LazyLoadEvent } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';
import { ProveedorSaludService } from './proveedorsalud.service';
import { DtoProveedores, DtoProveedoresData } from './dtoproveedores';

@Component({
    templateUrl: './proveedorsalud-listado.component.html'
})
export class ProveedorSaludListadoComponent extends BaseComponent {

    paginacion: ParametroPaginacion = new ParametroPaginacion();
    rows: number = 10;
    @ViewChild(ProveedorSaludMantenimientoComponent) proveedorSaludMantenimientoComponent: ProveedorSaludMantenimientoComponent;

    constructor(private proveedorSaludService: ProveedorSaludService) {
        super();
    }

    cargarProveedores(event: LazyLoadEvent) {

        this.bloquearPagina();

        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = this.rows;
        this.paginacion.listaResultado = [];

        this.proveedorSaludService.listarTodos(this.paginacion).then(
            res => {
                this.desbloquearPagina();
                this.paginacion = res;
            }
        );
    }

    nuevo(datatable: any) {
        this.tb = datatable;
        this.proveedorSaludMantenimientoComponent.nuevo();
    }

}



