import { SeguridadService } from './../seguridad/seguridad.service';
import { BaseComponent } from './../../comun/basecomponent';
import { Component, OnInit } from '@angular/core';
import { UsuarioActual } from '../usuarioactual';

@Component({
    moduleId: module.id,
    selector: 'bienvenida',
    templateUrl: 'bienvenida.component.html'
})
export class BienvenidaComponent extends BaseComponent implements OnInit {

    fechaInicio: Date = new Date();
    fechaInicio2: Date = new Date();
    usuarioActual: UsuarioActual = new UsuarioActual();
    fotografiaUsuario: string = "";
    verModal: boolean = false;
    traslados: any[] = [];
    headerConfig: any;


    esConductor: boolean = true;

    constructor() {
        super();
        this.esConductor = true;
    }

    ngOnInit() {
        console.log("BienvenidaComponent => " + this.blocked);
        this.esConductor = this.getUsuarioActual().rol == 2;
        this.headerConfig = {
            left: 'prev,next today',
            center: 'title',
            right: 'month,agendaWeek,agendaDay'
        };
        this.traslados = [
            {
                "title": "Ir a Av Universitaria 474, S. Miguel",
                "start": "2017-10-30T07:00:00",
                "end": "2017-10-30T08:00:00"
            },
            {
                "title": "Ir a Av San Luis 839, S. Surco",
                "start": "2017-10-30T10:00:00",
                "end": "2017-10-30T11:00:00"
            },
            {
                "title": "Ir a Av San Juan 474, SJM",
                "start": "2017-10-30T12:00:00",
                "end": "2017-10-30T14:00:00"
            },
            {
                "title": "Ir a Av. Benavides 234, Mir.",
                "start": "2017-10-31T16:00:00",
                "end": "2017-10-31T16:40:00"
            },
            {
                "title": "Ir a Av. Parto 123, Mir",
                "start": "2017-11-01T16:00:00",
                "end": "2017-11-01T17:00:00"
            }
        ];
        /*this.preCarga();
        this.verAniversarios();
        this.verCumpleanios();*/
    }
    preCarga() {
        this.usuarioActual = new UsuarioActual();
    }
    buscar() {
    }
    verCumpleanios() {
        this.bloquearPagina();
        if (this.fechaInicio == null) {
            this.fechaInicio = new Date();
        }

    }
    verAniversarios() {
        this.bloquearPagina();
        if (this.fechaInicio2 == null) {
            this.fechaInicio2 = new Date();
        }

    }

}


